body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

header{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-color: #36414d;
  height: 50px;
}

h1 {
  color: white;
  margin: 0;
  font-weight: bold;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: .025em;
  font-size: 32px;
  line-height: .9;
}

h2 {
  color: #4d5154;
  margin-top: 20px;
  margin-bottom: 30px;
  font-weight: bold;
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  font-size: 18px;  
}



